import 'bootstrap/dist/css/bootstrap.min.css';
import { Get } from 'react-axios';
import { Input } from 'antd';
import {
  Badge, ProgressBar, Container, Row, Col, Card, Button,
  ButtonGroup, OverlayTrigger, Popover
} from 'react-bootstrap';
import { useState } from 'react';

const { Search } = Input;
/**
 * 
 * @returns vdom
 */
function App() {
  // 
  const [index, setIndex] = useState(1);
  //  JSX(JavaScriptXML) JavaScript 
  // ()HtmlJS
  // {}JS
  return <>
    <Container>
      {/* 
      Axios,Ajax
      
      
      */}
      <Get url="http://localhost:4000/hero"
        params={{ index: index, num: 6 }}>
        {(error, response, isLoading, makeRequest, axios) => {
          if (isLoading) {
            return (<div>Loading...</div>)
          } else if (response !== null) {
            // 
            return (<>
              <Row>
                {/*  */}
                <Search
                  className='col-lg-6 my-3'
                  placeholder=""
                  allowClear
                  size='large'
                  enterButton=""
                  onSearch={(str) => {
                    console.log("q:" + str);
                    makeRequest({ params: { index: index, num: 6, q: str } });
                  }} />
                <ButtonGroup className='my-3 lg mx-auto col-lg-6'>
                  <Button
                    // 
                    onClick={() => {
                      // 
                      if (index != 1)
                        setIndex(index - 1)
                      makeRequest({ params: { index: index, num: 6 } })
                    }}>
                    
                  </Button>
                  <Button
                    onClick={() => makeRequest({ params: { index: index, num: 6 } })}
                    variant="info">
                    
                  </Button>
                  <Button
                    onClick={() => {
                      setIndex(index + 1)
                      makeRequest({ params: { index: index, num: 6 } })
                    }}>
                    
                  </Button>
                </ButtonGroup>

              </Row>
              {/*  */}
              <Row>
                {handleData(response.data.hero)}
              </Row>
            </>)
          }
          return (<div>Default message before request is made.</div>)
        }}
      </Get>
    </Container>
  </>
}

/**
 * 
 * @param {Array} hero 
 * @returns vdom
 */
const handleData = (hero) => hero.map((e, i) =>
  // mapHtml
  // bootstrapCSSclasstag
  <Col lg={2} sm={6} key={i.toString()} >
    <Card className='my-1 text-center'>
      {/*  */}
      <OverlayTrigger placement="bottom" overlay={popover(e)}>
        <Card.Img src={e.imgUrl} />
      </OverlayTrigger>
      <Card.Body>
        <Card.Title>{e.name}</Card.Title>
        <Card.Text>{e.title}</Card.Text>
        <audio id='audio'></audio>
        <Button onClick={() => {
          // 
          const audio = document.getElementById('audio')
          audio.src = e.selectAudio;// 
          audio.play();
        }} variant="outline-primary">Select</Button>
        <Button onClick={() => {
          const audio = document.getElementById('audio')
          audio.src = e.banAudio;// 
          audio.play();
        }} className="mx-1" variant="outline-primary">Ban</Button>
      </Card.Body>
    </Card>
  </Col>
)

/**
 *  
 * @param {hero} e 
 * @returns html
 */
const popover = (e) => (
  <Popover id="popover-basic" e={e}>
    <Popover.Header as="h3">{e.name + e.alias}</Popover.Header>
    {/*  */}
    <Popover.Body>
      <ProgressBar variant="danger" now={e.attack + '0'} label='' />
      <ProgressBar variant="success" now={e.defence + '0'} label='' />
      <ProgressBar variant="info" now={e.magic + '0'} label='' />
      <ProgressBar variant="warning" now={e.difficulty + '0'} label='' />
      {e.roles.map((r, i) => <Badge key={i.toString()} bg="primary" className='my-2 mx-1'>{r}</Badge>)}
    </Popover.Body>
  </Popover>
);

// 
export default App;
